  width: calc(var(--star-width) * var(--star-count) + var(--star-offset) * var(--star-offset-count));
}

.toggle__title {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  margin-bottom: 16px;
}
.toggle__item {
  margin-bottom: 14px;
}
.toggle__input {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}
.toggle__input:focus + .toggle__display {
  outline: 1px dotted #212121;
  outline: 1px auto -webkit-focus-ring-color;
}
.toggle__input__input:focus:not(:focus-visible) + .toggle__display {
  outline: none;
}
.toggle__input:checked + .toggle__display {
  border-color: var(--color-purple);
}
.toggle__input:checked + .toggle__display::before {
  left: 61%;
  background: var(--color-gradient);
}
.toggle__label {
  display: flex;
  align-items: center;
  font: var(--text);
  line-height: 0;
  color: var(--color-dark-75);
  cursor: pointer;
  gap: 10px;
}
.toggle__display {
  position: relative;
  border: 1px solid var(--color-dark-25);
  border-radius: 10px;
  width: 40px;
  height: 20px;
}
.toggle__display::before {
  content: "";
  position: absolute;
  left: 8%;
  top: 50%;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background: var(--color-dark-25);
  transform: translateY(-50%);
}

.like-button__title {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  margin-bottom: 16px;
}
.like-button___container {
  display: flex;
