.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 22px;
  padding: 0;
  width: 100%;
  height: 44px;
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-light);
  background: var(--color-gradient);
  cursor: pointer;
}
.btn:disabled {
  cursor: auto;
}
.btn__fill:hover, .btn__fill:disabled {
  color: var(--color-light);
  background: var(--color-gradient-opacity);
}
.btn__border span {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 22px;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  color: var(--color-purple);
  background: var(--color-light);
}
.btn__border:hover, .btn__border:disabled {
  background: var(--color-gradient-opacity);
}
.btn__text {
  width: auto;
  height: auto;
  color: var(--color-purple);
  background: transparent;
}
.btn__text:hover, .btn__text:disabled {
  color: var(--color-dark-50);
}
.btn__arrow {
  background: url("../img/components/button/arrow_forward.svg") no-repeat calc(100% - 13px) 50%, var(--color-gradient);
}

.subtitle {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
}

.expandable-checkbox-list {
  --height-btn: 45px;
  position: relative;
  max-width: 100%;
}
.expandable-checkbox-list__btn {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 2px;
  border: none;
  padding: 0;
  padding-right: 16px;
  width: 100%;
  height: var(--height-btn);
  background: var(--color-light);
  cursor: pointer;
}
.expandable-checkbox-list__svg {
  fill: var(--color-dark-50);
}
.expandable-checkbox-list__list {
  position: absolute;
  left: 0;
  top: var(--height-btn);
  width: 100%;
  display: flex;
  flex-direction: column;
  opacity: 0;
  visibility: hidden;
  background: var(--color-light);
  gap: 4px;
}

.expandable-checkbox-list--open .expandable-checkbox-list__list {
  opacity: 1;
  visibility: visible;
}
.expandable-checkbox-list--open .expandable-checkbox-list__svg {
  transform: rotate(-180deg);
}

