@mixin form-subtitle {
  font: var(--sub-title);
  text-transform: uppercase;
  color: var(--color-dark-100);
}

@mixin title {
  font-family: var(--font-family);
  font-size: 24px;
  color: var(--color-dark-100);
}

@mixin visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}
